import React from 'react';
import { sendAxiosRequest } from '../apis/sendRequest';

//  Select request,   

const channelsRequestGet = () => {
  //  Get 
  return new Promise(function (resolve, reject) {
    const api = 'channel/v1/channels';
    const method = 'get';
    const reqParams = {};
    const reqObject = {};
    sendAxiosRequest(api, method, reqParams, reqObject)
      .then(respObj => {
        resolve(respObj);
      })
      .catch(err => {
        console.log(err);
        reject(err);
      });
  });
};

