import React, { useEffect, useState } from 'react';
// 

const computeCurrentScrollTop = () => {
    // 
    if (typeof document !== 'undefined') {
        const scrollTop = document.documentElement.scrollTop || document.body.scrollTop; //;
        // const clientHeight = document.documentElement.clientHeight || document.body.clientHeight; // ;
        // const pageHeight = document.body.scrollHeight || document.documentElement.scrollHeight || document.documentElement.offsetHeight; // 
        return scrollTop;
    }
    return 0
}

const scrollToTopSize = (topSize) => {
    // 
    if (typeof window !== 'undefined') {
        setTimeout(() => {
            // console.log(topSize);
            // console.log('body');
            window.scrollTo({ 
                top: parseInt(topSize), 
                behavior: "smooth" 
            });
        }, 500)
    }
}

const ScrollGoBack = ({visible}) => {
    // visible > 0 visible == 0 
    const [currentScrollTop, setCurrentScrollTop] = useState(0);

    useEffect(() => {
        if (visible > 0) {
            // 
            const currentTop = computeCurrentScrollTop();
            // console.log(currentTop);
            setCurrentScrollTop(currentTop);
        }
        if (visible === 0) {
            // 
            scrollToTopSize(currentScrollTop);
        }
    }, [visible])
    return <></>
}
ScrollGoBack.defaultProps = {
    visible: 0
}

export { ScrollGoBack };